import React, { Component } from 'react';
import Nav from 'react-bootstrap/Nav';
import NavBar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import 'bootstrap/dist/css/bootstrap.min.css';

import './NavBarArea.css';

import { Link, withRouter } from "react-router-dom";

import Icon from '../../components/Icon/Icon.js';


import logoImage from '../../img/logo.png';
import languageIcon from '../../img/language.svg';


class NavBarArea extends Component {
    constructor(props) {
        super(props);
        var checkUrl = new URL(window.location.href);
        var lang = (checkUrl.searchParams.get("lang") || "").toLowerCase();
        if (lang == "en" || lang == "en-us") {
            this.props.languageChange("en");
        }
        else if (lang == "ko" || lang == "ko-kr") {
            this.props.languageChange("ko");
        }
        else {
            var checkUrl = new URL(window.location.href);
            checkUrl.searchParams.delete("lang");
            window.history.replaceState(null, null, checkUrl.href);
        }
    }

    T(en, ko) {
        if (this.props.language == "en") {
          return en;
        }
        else {
          return ko;
        }
    }

    getPath(path) {
        var newUrl = new URL(window.location.href);
        newUrl.pathname = path;
        newUrl.searchParams.delete("download");
        return newUrl.pathname + newUrl.search;
    }

    getCleanUrl(path) {
        var checkUrl = new URL(window.location.href);
        var lang = checkUrl.searchParams.get("lang");
        checkUrl.pathname = path;
        var params = new URLSearchParams();
        var defaultLang = "en";
        if (["en", "ko"].includes(lang)) {
            if (lang != defaultLang) {
            params.set("lang", lang);
            }
        }
        checkUrl.search = params.toString();
        return checkUrl.href;
    }

    loadPage(path) {
        window.location.href = this.getCleanUrl(path);
    }

    render() {
        return (
            <div className="NavBar">
                <div className="nav-bar">
                    <NavBar collapseOnSelect fixed="top" bg="light" expand="lg">
                        <Link className="navbar-brand" onClick={() => this.loadPage("/")}>
                            <img className="nav-icon" src={logoImage} alt="TwitchLink" />
                            TwitchLink
                        </Link>
                        <NavBar.Toggle/>
                        <NavBar.Collapse id="basic-navbar-nav">
                            <Nav className="mr-auto" activeKey={this.props.location.pathname}>
                                <Nav.Item>
                                    <Nav.Link eventKey="/" as={Link} to={this.getPath("/")}>
                                        TwitchLink
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="/download/" as={Link} to={this.getPath("/download/")}>
                                        {this.T("Download", "다운로드")}
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="/help/" as={Link} to={this.getPath("/help/")}>
                                        {this.T("Manual & Help", "사용법 & 도움말")}
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="/support/" href="https://github.com/devhotteok/TwitchLink" target="_blank">
                                        {this.T("Support & Issues", "지원 & 버그 제보")}
                                    </Nav.Link>
                                </Nav.Item>
                                <Nav.Item>
                                    <Nav.Link eventKey="/donate/" as={Link} to={this.getPath("/donate/")}>
                                        {this.T("Donate", "후원")}
                                    </Nav.Link>
                                </Nav.Item>
                            </Nav>
                            <Nav>
                                <NavDropdown alignRight title={
                                    <div style={{display: "flex"}}>
                                        <Icon size="small" src={languageIcon} alt="Language" style={{marginRight: "10px"}}/>
                                        Language / 언어
                                    </div>
                                }>
                                    <NavDropdown.Item onClick={() => this.props.languageChange("en")}>ENGLISH</NavDropdown.Item>
                                    <NavDropdown.Item onClick={() => this.props.languageChange("ko")}>한국어</NavDropdown.Item>
                                </NavDropdown>
                            </Nav>
                        </NavBar.Collapse>
                    </NavBar>
                    <div className="nav-bar-area"></div>
                </div>
            </div>
        );
    }
}

export default withRouter(NavBarArea);