import React from 'react';

import './Label.css';

const Label = (props) => {
    var { className, ...rest } = props;
    if (className == undefined) {
        className = "Label";
    }
    else {
        className += " Label";
    }
    return (
        <div className={className} {...rest}>
            {props.children}
        </div>
    );
}

export default Label;