import React from "react";

import "./FooterArea.css";

const FooterArea = () => {
  var DEVELOPER = "DevHotteok";
  var FIRST_RELEASE_YEAR = 2021;
  var thisYear = new Date().getFullYear();
  if (FIRST_RELEASE_YEAR < thisYear) {
    var year = thisYear;
  }
  else {
    var year = FIRST_RELEASE_YEAR;
  }
  return (
    <footer className="FooterArea">
      <div>&copy; {year} {DEVELOPER}.</div>
      <div className="contact">
        <div style={{margin: "0 10px"}}>
          Discord : DevHotteok#6437
        </div>
        <div>
          Email : devhotteok@gmail.com
        </div>
      </div>
    </footer>
  );
};

export default FooterArea;