import React from "react";

import "./Button.css";

const Button = (props) => {
  var { className, ...rest } = props;
  if (className == undefined) {
    className = "Button";
  }
  else {
    className += " Button";
  }
  return (
    <button className={className} {...rest}>
      {props.children}
    </button>
  );
};

export default Button;