import React, { Component } from 'react';

import "./ErrorBoundary.css";

import Label from "../../components/Labels/Label/Label.js";
import Button from "../../components/Buttons/index.js";

export default class ErrorBoundary extends Component {
    constructor(props) {
      super(props);
      this.state = { hasError: false };
    }

    T(en, ko) {
      if (this.props.language == "en") {
        return en;
      }
      else {
        return ko;
      }
    }
  
    static getDerivedStateFromError(error) {
      return { hasError: true };
    }
  
    render() {
      if (this.state.hasError) {
        return (
          <div className="ErrorBoundary">
            <div style={{padding: "50px 0"}}>
              <Label style={{margin: "0 auto", fontSize: "15pt", color: "gray"}}>{this.T("Oops! Something went wrong. :(", "문제가 발생하였습니다.")}</Label>
              <Label style={{margin: "10px auto", fontSize: "13pt", color: "gray"}}>{this.T("Try refreshing the page.", "페이지를 새로고침 해보세요.")}</Label>
              <Button.Button style={{margin: "30px auto"}} onClick={() => window.location.reload()}>{this.T("Refresh", "새로고침")}</Button.Button>
            </div>
          </div>
        );
      }
      return this.props.children;
    }
}