import React from 'react';

import './ButtonArea.css';

const ButtonArea = (props) => {
    var { className, ...rest } = props;
    if (className == undefined) {
        className = "ButtonArea";
    }
    else {
        className += " ButtonArea";
    }
    return (
        <div className={className} {...rest}>
            {props.children}
        </div>
    );
}

export default ButtonArea;