import React from "react";

import "./Icon.css";

import ImageLabel from "../../components/Labels/ImageLabel/ImageLabel.js";

const Icon = (props) => {
  var newClassName = "Icon";
  switch (props.size) {
    case "tiny":
      newClassName = "IconTiny";
      break;
    case "small":
      newClassName = "IconSmall";
      break;
    case "big":
      newClassName = "IconBig";
      break;
    case "fit":
      newClassName = "IconFit";
      break;
  }
  var { className, ...rest } = props;
  if (className == undefined) {
    className = newClassName;
  }
  else {
    className += " " +  newClassName;
  }
  return <ImageLabel className={className} {...rest}/>;
};

export default Icon;