import React from 'react';

import "./Loading.css";

import Label from "../../components/Labels/Label/Label.js";

const Loading = (props) => {
  var { className, ...rest } = props;
  if (className == undefined) {
    className = "Loading";
  }
  else {
    className += " Loading";
  }
  return (
    <div className={className} {...rest}>
      <div style={{padding: "50px 0"}}>
        <Label style={{margin: "0 auto", fontSize: "13pt", color: "gray"}}>
          {props.children}
        </Label>
      </div>
    </div>
  );
};

export default Loading;