import React from 'react';

import './ImageLabel.css';

const ImageLabel = (props) => {
    var { className, ...rest } = props;
    if (className == undefined) {
        className = "ImageLabel";
    }
    else {
        className += " ImageLabel";
    }
    return (
        <img className={className} {...rest}/>
    );
}

export default ImageLabel;